

















































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Form extends Vue {
  form = {
    name: '',
    region: '',
    date1: '',
    date2: '',
    delivery: false,
    type: [],
    resource: '',
    desc: '',
  };

  onSubmit() {
    this.$message('submit!');
  }

  onCancel() {
    this.$message({
      message: 'cancel!',
      type: 'warning',
    });
  }
}
